body{
  background-color: #282c34;
}
.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.Button {
  width: 150px;
  height: 70px;
  font-weight: 700;
  font-size: 20px;
  border-radius: 0.25rem;
  color:white;
  border: none;
}

.Button--right, .Button--left {
  border: 1.5px solid rgb(66, 135, 180);
  background-color: rgb(66, 135, 180);
}

.Buttons--container{
  width: 90%;
  margin: 0 auto;
  display: flex;
  justify-content: space-around;

}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

:focus{
  outline: none;
  border: 1.5px solid rgb(66, 135, 180);
}